import { Box,  Heading, Text } from '@chakra-ui/react'
import React from 'react'

function About() {
  return (
    <Box  m='30px 0  0' id='about' mt={'-90px'}>
      <Heading textAlign={'center'} style={{color:'rgb(243, 14, 79)'}}>About Me</Heading>
      <Box w={{base:'90%',md:'80%',lg:'70%'}} m="40px auto 0" lineHeight={{base:'7',md:'10'}} >
      <Text fontSize={{base:16,md:18,lg:20}} textAlign={'center'} fontFamily='cursive'>
        Hi, I'm Malay Bajpai. I have completed my engineering in Automobile branch. I'm an aspiring
        Full stack web developer,capable of writing production-ready code using HTML,
        CSS, JavaScript and React-Js and a systematic person who likes to work in a methodical way. Looking forward to the
        opportunities that can bring challenges and learnings to leverage and hone my skills.
        </Text>
    

      </Box>
    </Box>
  )
}

export default About